<!-- 修改 by czh 查看答卷情况 -->
<template>
    <div class="quest" style="min-height:85vh;">
        <div id="top"/>
        <el-table
        :data="tableData"
        :span-method="objectSpanMethod"
        style="width: 100%">
        <el-table-column
            prop="name"
            label="姓名">
        </el-table-column>
        <el-table-column
            prop="time"
            label="时间">
        </el-table-column>
        <el-table-column v-for="item in ['Q1','Q2','Q3','Q4','Q5','Q6','Q7','Q8','Q9','Q10','Q11','Q12','Q13','Q14','Q15','Q16']" :key="item" :prop="item" :label="item">
        </el-table-column>
        <el-table-column
            prop="DSocre"
            label="DSocre">
        </el-table-column>
        <el-table-column
            prop="NScore"
            label="NScore">
        </el-table-column>
        <el-table-column
            prop="OScore"
            label="OScore">
        </el-table-column>
        <el-table-column
            prop="TSScore"
            label="TSScore">
        </el-table-column>
        </el-table>
    </div>
</template>

<script>
    
    export default {
        data() {
            return {
                tableData:[]
            }
        },
        methods: {
            init(){
                let data = window.localStorage.getItem('userAccountScore')
                data = JSON.parse(decodeURIComponent(data))
                console.log(data)
                let dataList = []
                data.forEach(item => {
                    dataList.push({...item.first,...item.firstCalc,time:item.time,name:item.name})
                    dataList.push({...item.second,...item.secondCalc,time:item.time,name:item.name})
                })
                console.log(dataList)
                this.tableData = JSON.parse(JSON.stringify(dataList))
            },
            objectSpanMethod({ rowIndex, columnIndex }) {
                if (columnIndex === 0 || columnIndex === 1) {
                    if (rowIndex % 2 === 0) {
                        return {
                        rowspan: 2,
                        colspan: 1
                        };
                    } else {
                        return {
                        rowspan: 0,
                        colspan: 0
                        };
                    }
                }
            }
        },
        mounted(){
            this.init()
        }
    }
</script>

